export default {
	props: {
		show: {
			type: [Boolean],
			default: true,
		},
		list: {
			type: Array,
			default: []
		},
		index: {
			type: Number,
			default: 0
		}
	},

	data() {
		return {
			sw: false,
			indicatorDots: false,
			autoplay: false,
			duration: 500,
			circular: true,
			currentIndex: -1,
		};
	},
	mounted() {},
	methods: {

		saveFile() {
			let index = this.currentIndex != -1 ? this.currentIndex : this.index;
			console.log('尝试保存', this.list[index])


















			console.log('尝试saveImageToPhotosAlbum保存')
			uni.downloadFile({
				url: this.list[index],
				success: (res) => {
					uni.saveImageToPhotosAlbum({
						filePath: res.tempFilePath,
						success: function() {
							console.log('save success');
							uni.showToast({
								title: '图片保存成功',
								icon: 'none',
								duration: 1200
							});
						}
					});
				}
			})


		},
		hide() {
			this.currentIndex = -1;
			this.sw = !this.sw;
		},
		changeSwiper(e) {
			console.log(this.currentIndex, this.index)
			this.currentIndex = e.target.current;
		},
	}
}
